[data-box] {
	/* -------------------------------------------- */
	/* variable
	/* -------------------------------------------- */
	$box: ( denom: 12, gutterMaxnum: 8 );
	/* -------------------------------------------- */
	/* setup
	/* -------------------------------------------- */
	display: flex;
	flex-wrap: wrap;
	max-width: $siteWidth;
	& > * {
		flex: 1;
	}
	/* item base size
	/* ------------------- */
	&[data-box~='auto'] {
		& > * {
			flex: 1;
			width: auto;
		}
	}
	&[data-box~='full'] {
		& > * {
			flex: auto;
			width: 100%;
		}
	}
	&[data-box~='inline'] {
		& > * {
			flex: 0;
			width: auto;
		}
	}
	@for $i from 1 through map-get($box, denom) {
		 @for $c from 1 through map-get($box, denom){
			@if $c <= $i {
				&[data-box~='#{$c}/#{$i}'] {
					& > * {
						flex: 0 0 auto;
						width: 100% / ($i / $c);
					}
				}
			}
		}
	}
	@if $responsive {
		 @each $key in $respondToKey{
			@include respondTo($key) {
				 @for $i from 1 through map-get($box, denom){
					@for $c from 1 through map-get($box, denom) {
						 @if $c <= $i{
							&[data-box~='#{$key}-auto'] {
								& > * {
									flex: 1;
									width: auto;
								}
							}
							&[data-box~='#{$key}-full'] {
								& > * {
									flex: auto;
									width: 100%;
								}
							}
							&[data-box~='#{$key}-inline'] {
								& > * {
									flex: 0;
									width: auto;
								}
							}
							&[data-box~='#{$key}-#{$c}/#{$i}'] {
								& > * {
									flex: 0 0 auto;
									width: 100% / ($i / $c);
								}
							}
						}
					}
				}
			}
		}
	}
	/* gutter
	/* ------------------- */
	@for $size from 1 through map-get($box, gutterMaxnum) {
		&[data-box~='gutter-#{$size}'] {
			margin-left: size(-$size / 2);
			margin-right: size(-$size / 2);
			& > * {
				padding-bottom: size($size);
				padding-left: size($size / 2);
				padding-right: size($size / 2);
			}
		}
		&[data-box~='gutter-none'] {
			margin: 0;
			& > * {
				padding-bottom: 0;
				padding-left: 0;
				padding-right: 0;
			}
		}
	}
	@for $size from 1 through map-get($box, gutterMaxnum) {
		 @for $bottomSize from 1 through map-get($box, gutterMaxnum){
			&[data-box~='gutter-#{$size}-#{$bottomSize}'] {
				margin-left: size(-$size / 2);
				margin-right: size(-$size / 2);
				& > * {
					padding-bottom: size($bottomSize);
					padding-left: size($size / 2);
					padding-right: size($size / 2);
				}
			}
			&[data-box~='gutter-#{$size}-none'] {
				margin-left: size(-$size / 2);
				margin-right: size(-$size / 2);
				& > * {
					padding-bottom: 0;
					padding-left: size($size / 2);
					padding-right: size($size / 2);
				}
			}
			&[data-box~='gutter-none-#{$bottomSize}'] {
				margin: 0;
				& > * {
					padding-bottom: size($bottomSize);
					padding-left: 0;
					padding-right: 0;
				}
			}
		}
	}
	@if $responsive {
		 @each $key in $respondToKey{
			@include respondTo($key) {
				 @for $size from 1 through map-get($box, gutterMaxnum){
					&[data-box~='#{$key}-gutter-#{$size}'] {
						margin-left: size(-$size / 2);
						margin-right: size(-$size / 2);
						& > * {
							padding-bottom: size($size);
							padding-left: size($size / 2);
							padding-right: size($size / 2);
						}
					}
				}
				@for $size from 1 through map-get($box, gutterMaxnum) {
					 @for $bottomSize from 1 through map-get($box, gutterMaxnum){
						&[data-box~='#{$key}-gutter-#{$size}-#{$bottomSize}'] {
							margin-left: size(-$size / 2);
							margin-right: size(-$size / 2);
							& > * {
								padding-bottom: size($size);
								padding-left: size($size / 2);
								padding-right: size($size / 2);
							}
						}
						&[data-box~='#{$key}-gutter-#{$size}-none'] {
							margin-left: size(-$size / 2);
							margin-right: size(-$size / 2);
							& > * {
								padding-bottom: 0;
								padding-left: size($size / 2);
								padding-right: size($size / 2);
							}
						}
					}
				}
			}
		}
	}
	/* justify-content
	/* ------------------- */
	&[data-box~='left'] {
		justify-content: flex-start;
	}
	&[data-box~='center'] {
		justify-content: center;
	}
	&[data-box~='right'] {
		justify-content: flex-end;
	}
	@if $responsive {
		 @each $key in $respondToKey{
			@include respondTo($key) {
				&[data-box~='#{$key}-left'] {
					justify-content: flex-start;
				}
				&[data-box~='#{$key}-center'] {
					justify-content: center;
				}
				&[data-box~='#{$key}-right'] {
					@include respondTo($key) {
						justify-content: flex-end;
					}
				}
			}
		}
	}
	/* item
	/* ------------------- */
	& > [data-box-item~='auto'] {
		flex: 1;
		width: auto;
	}
	& > [data-box-item~='full'] {
		flex: auto;
		width: 100%;
	}
	& > [data-box-item~='inline'] {
		flex: 0;
		width: auto;
	}
	& > [data-box-item~='left'] {
		margin-right: auto;
	}
	& > [data-box-item~='right'] {
		margin-left: auto;
	}
	& > [data-box-item~='center'] {
		margin-right: auto;
		margin-left: auto;
	}
	@for $i from 1 through map-get($box, denom) {
		 @for $c from 1 through map-get($box, denom){
			& > [data-box-item~='#{$c}/#{$i}'] {
				flex: 0 0 auto;
				width: 100% / ($i / $c);
			}
		}
	}
	@if $responsive {
		 @each $key in $respondToKey{
			@include respondTo($key) {
				& > [data-box-item~='#{$key}-auto'] {
					flex: 1;
					width: auto;
				}
				& > [data-box-item~='#{$key}-full'] {
					flex: auto;
					width: 100%;
				}
				& > [data-box-item~='#{$key}-inline'] {
					flex: 0;
					width: auto;
				}
				& > [data-box-item~='#{$key}-left'] {
					margin: 0 auto 0 0;
				}
				& > [data-box-item~='#{$key}-right'] {
					margin: 0 0 0 auto;
				}
				& > [data-box-item~='#{$key}-center'] {
					margin: 0 auto;
				}
				@for $i from 1 through map-get($box, denom) {
					 @for $c from 1 through map-get($box, denom){
						& > [data-box-item~='#{$key}-#{$c}/#{$i}'] {
							flex: 0 0 auto;
							width: 100% / ($i / $c);
						}
					}
				}
			}
		}
	}
}
