/* foundation/vendor-extensinon/fontAwesome
/* ============================================================================================== */
/*
---
name: Font Awesome
category: 1.foundation/vendor-extension
---
[fontAwesome](http://fontawesome.io/icons/)

### Class

```html
<i class="fa fa-coffee" aria-hidden="true"></i>
```

### Mixin

`$char`[fontAwesome](http://fontawesome.io/icons/)` : \f2b4``$insert``before``after`

```scss
sample {
	@include fontAwesome($char, $insert);
}
```
*/
@mixin fontAwesome($char, $insert: before) {
	&:#{$insert} {
		font-family: FontAwesome;
		content: $char;
	}
}
