/* ============================================================================================== */
/* foundation/mixin/respondTo
/* ============================================================================================== */
/*
---
name: Respond To
category: 1.foundation/mixin
tag:
- responsive
- mixin
---
`foundation $breakpoint`

```scss
@include respondTo(smAndUnder) { small};
@include respondTo(mdAndUnder) { mediul};
@include respondTo(smAndOver) { small};
@include respondTo(smAndUnder) { mediul};
@include respondTo(xs) { xsmall(small)};
@include respondTo(sm) { small};
@include respondTo(md) { medium};
@include respondTo(lg) { large};
```
*/
/*
---
name: breakpoint prefix
category: 0-1.HTML
tag: responsive
---

responsive`component data-**``utility data-**``breakpoint prefix`

- `smAndOver-`:`breakpointSmall`
- `mdAndOver-`:`breakpointMedium`
- `smAndUnder-`:`breakpointSmall`
- `mdAndUnder-`:`breakpointMedium`
- `smAndUnder-`:`breakpointSmall`
- `xs-`:`breakpointXSmall`
- `sm-`:`breakpointSmall`
- `md-`:`breakpointMedium`
- `lg-`:`breakpointLarge`
*/
/* -------------------------------------------- */
/* mixin
/* -------------------------------------------- */
@mixin respondTo($key) {
	$breakpointSmall: map-get($breakpoint, small);
	$breakpointMedium: map-get($breakpoint, medium);
	$breakpointLarge: map-get($breakpoint, large);
	$breakpointXSmallMax: ($breakpointSmall - 1);
	$breakpointSmallMax: ($breakpointMedium - 1);
	$breakpointMediumMax: ($breakpointLarge - 1);
	/* -------------------------------------------- */
	/* setup
	/* -------------------------------------------- */
	/* only
	/* ------------------- */
	@if $key == xs {
		 @media (max-width: $breakpointXSmallMax){
			@content;
		}
	}
	@else if $key == sm {
		 @media (min-width: $breakpointSmall) and (max-width: $breakpointSmallMax){
			@content;
		}
	}
	@else if $key == md {
		 @media (min-width: $breakpointMedium) and (max-width: $breakpointMediumMax){
			@content;
		}
	}
	@else if $key == lg {
		 @media (min-width: $breakpointLarge){
			@content;
		}
	}
	/* over
	/* ------------------- */
	@else if $key == smAndOver {
		 @media (min-width: $breakpointSmall){
			@content;
		}
	}
	@else if $key == mdAndOver {
		 @media (min-width: $breakpointMedium){
			@content;
		}
	}
	/* under
	/* ------------------- */
	@else if $key == smAndUnder {
		 @media (max-width: $breakpointSmallMax){
			@content;
		}
	}
	@else if $key == mdAndUnder {
		 @media (max-width: $breakpointMediumMax){
			@content;
		}
	}
}
/* -------------------------------------------- */
/* respondToKeyArray
/* -------------------------------------------- */
$respondToKey: ( smAndOver, mdAndOver, smAndUnder, mdAndUnder, xs, sm, md, lg);
